@charset "UTF-8";
body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  font-size: 100%;
  font-family: Arial, sans-serif;
}

.page {
  background-image: url(../images/ep_naturalwhite.png);
}

.wrapper {
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.container {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.debug-grid {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug-grid > div {
  height: inherit;
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.debug-grid > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.debug-grid > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(8.33333% - 30px);
  height: inherit;
  outline: #000;
  background: rgba(255, 0, 0, 0.2);
}

.navbar {
  flex-basis: 100%;
  position: relative;
}

.navbar .input.toggleMenu[type="checkbox"], .navbar .input.toggleSubMenu[type="checkbox"] {
  display: none;
}

.navbar .label {
  display: block;
}

.navbar .label.toggleSubMenu {
  position: absolute;
  top: 0;
  right: 40px;
}

.icon {
  position: absolute;
  top: 13px;
  left: 18px;
  font-size: 18px;
}

.icon.icon-down-open {
  font-size: 12px;
}

.icon.icon-down-open:before {
  transition: all 0.2s;
}

.menu {
