/* =============================
   Настройка адаптивного меню
============================= */
.label.toggleMenu {
  background: linear-gradient(to bottom, #eee 0%, #b1b1b1 100%);
  display: block;
  padding: 15px 45px;
  text-transform: uppercase;
  font-size: 14px;
  cursor: pointer;
  position: relative;
  display: none;
}

@media all and (max-width: 800px) {
  .menu {
    overflow: hidden;
    display: none;
  }
  .submenu-1 {
    right: auto;
    top: auto;
  }
  .submenu-1__item {
    margin: 0;
  }
  .submenu-2 {
    right: auto;
    top: auto;
  }
  .submenu-2__item {
    margin: 0;
  }
  .submenu-3 {
    right: auto;
    top: auto;
  }
  .submenu-3__item {
    margin: 0;
  }
  .menu,
  .submenu-1 {
    display: block;
    max-height: 0;
    transition: max-height 0.5s;
  }
  .menu,
  .submenu-2 {
    display: block;
    max-height: 0;
    transition: max-height 0.5s;
  }
  .menu,
  .submenu-3 {
    display: block;
    max-height: 0;
    transition: max-height 0.5s;
  }
  .navbar .label {
    display: block;
  }
  .navbar .label.toggleSubMenu {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    display: block;
  }
  .navbar .label.toggleSubMenu > .icon {
    position: relative;
    display: block;
  }
  .navbar .label.toggleSubMenu > .icon:before {
    position: absolute;
    right: 30px;
    transition: all 0.2s;
  }
  .navbar .input:checked + .label {
    background-image: linear-gradient(to bottom, #7d7e7d 0%, #0e0e0e 100%);
    color: #fff;
  }
  .navbar .input.toggleMenu:checked ~ .menu,
  .navbar .input.toggleMenu:checked ~ .submenu-1, .navbar .input.toggleSubMenu:checked ~ .menu,
  .navbar .input.toggleSubMenu:checked ~ .submenu-1 {
    display: block;
    position: relative;
    max-height: 5000px;
    transition: max-height 2s ease-in;
  }
  .navbar .input.toggleMenu:checked ~ .menu__link,
  .navbar .input.toggleMenu:checked ~ .submenu-1__link, .navbar .input.toggleSubMenu:checked ~ .menu__link,
  .navbar .input.toggleSubMenu:checked ~ .submenu-1__link {
    background: linear-gradient(to bottom, #7d7e7d 0%, #0e0e0e 100%);
    color: #fff;
  }
  .navbar .input.toggleMenu:checked ~ .menu,
  .navbar .input.toggleMenu:checked ~ .submenu-2, .navbar .input.toggleSubMenu:checked ~ .menu,
  .navbar .input.toggleSubMenu:checked ~ .submenu-2 {
    display: block;
    position: relative;
    max-height: 5000px;
    transition: max-height 2s ease-in;
  }
  .navbar .input.toggleMenu:checked ~ .menu__link,
  .navbar .input.toggleMenu:checked ~ .submenu-2__link, .navbar .input.toggleSubMenu:checked ~ .menu__link,
  .navbar .input.toggleSubMenu:checked ~ .submenu-2__link {
    background: linear-gradient(to bottom, #7d7e7d 0%, #0e0e0e 100%);
    color: #fff;
  }
  .navbar .input.toggleMenu:checked ~ .menu,
  .navbar .input.toggleMenu:checked ~ .submenu-3, .navbar .input.toggleSubMenu:checked ~ .menu,
  .navbar .input.toggleSubMenu:checked ~ .submenu-3 {
    display: block;
    position: relative;
    max-height: 5000px;
    transition: max-height 2s ease-in;
  }
  .navbar .input.toggleMenu:checked ~ .menu__link,
  .navbar .input.toggleMenu:checked ~ .submenu-3__link, .navbar .input.toggleSubMenu:checked ~ .menu__link,
  .navbar .input.toggleSubMenu:checked ~ .submenu-3__link {
