    color: #fff;
  }
  .navbar .input.toggleMenu:checked ~ .label.toggleSubMenu .icon-down-open:before, .navbar .input.toggleSubMenu:checked ~ .label.toggleSubMenu .icon-down-open:before {
    transform: rotate(-180deg);
    color: #fff;
